{
  margin: 0;
  padding: 0;
  box-sizing: border-box
}
body {
    font-family: 'montserrat', l-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  background-color: #fff;
  background: url(Components/Images/movieTheaterPopCorn.jpg);
  background-color: rgba(0, 0, 0, 0.75);
  background-blend-mode: multiply;
}

header {
  padding: 50px 15px 0px;
} 

header h1 {
  color: #e0deec;
  font-size: 50px;
  font-weight: 500;
  text-align: center;
}

main {
  width: 100%;
  max-width: 960px;
  margin: auto;
}

section {
  padding: 15px;
}

.searchbox {
  display: block;
  width: 100%;
  padding: 15px;
  
  border: 10p;
  outline: none;
  background: none;

  background-color: #eee;
  border-radius: 10px;

  color: #53565a;
  font-size: 20px;
  font-weight: 300;

  transition: 0.4s ease-out;
}

.searchbox:focus {
  box-shadow: 0px 0px 10px 5px #444888;
}

.results {
  display: flex;
  flex-wrap: wrap;
  margin: 0px - 15px;
  margin: center;
}

.results .result {
  width: 33.33%;
  min-width: 250px;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.results .result img {
  display: block;
  width: 100%;
}

.results .result h3 {
  color: #444824;
  font-size: 20px;
  font-weight: 600;
  padding: 15px;
  background: #002246;
  flex: 1 1 100%;
  transition: 0.4s ease-out;
 }

.results .result:hover h3 {
  background: #f43f37e8;
  color: #f3f3f3
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #8e5ddb;

  overflow-y: scroll;
  }

  .popup .content {
    display: block;
    width: 100%;
    max-width: 960px;
    margin: 0 auto;
    padding: 25px;

    color: #121212;
    
  }
  .popup .content h2 {
    font-size: 28px;
    font-weight: 600;
  }

  .popup .content h2 span {
    font-weight: 300;
    font-size: 20px;
  }

  .popup .content .rating {
    margin-bottom: 30px;
  }

  .popup .content .plot {
    display: flex;
    flex-wrap: wrap;
    margin: 0px -15px 30px;
  }

  .popup .content .plot img {
    flex: 1 1 50%;
    max-width: 300px;
    padding: 0px 15px;
  }

  .popup .content .plot p {
    flex: 1 1 50%;
    padding: 15px;
  }

  .popup .content .close {
    display: inline-block;
    padding: 15px 30px;
    font-size: 18px;
    font-weight: 700;
  
    background: #130101;
    color: #fff;
    border-radius: 8px;
    border: none;
    outline: none;
    appearance: none;
    cursor: pointer;

    transition: 0.4s ease-out;
  }

  .popup .content .close:hover {
    background: #8888;
  }

